import React, { Component } from 'react';
import Sequence from '../Sequence';
import Period from '../Period';
import Action from '../Action';
import Image from '../Image'
import Fx from '../Fx';
import '../../styles.css'
import './index.css';
class SmallImage extends Component{
    render(){
        const {id, sequence, action, place, periode, fx, url, color, colorDistribution} = this.props.file
        let backgroundColor
        if(colorDistribution === "full"){
            backgroundColor= color
        }else{
            backgroundColor= null
        }
        return (
            <div className="tpl-small ele" style={{'background-color': backgroundColor}}>
                <Image url={url}/>
                <div className="legend">
                    <div className="indicators">
                        <Sequence color={color} value={sequence}/>
                        <Period value={periode}/>
                        <Fx value={fx}/>
                    </div>
                    <Action value={action}/>
                </div>
            </div>
        )
    }
}

export default SmallImage;



